@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600;900&family=Poppins:wght@400;700&family=Tangerine:wght@700&display=swap);
.alice-carousel {
  font-size: 2rem !important;
}

.alice-carousel__dots-item {
   background-color: #065471 !important;
   width: 1rem !important;
   height: 1rem !important;
}

.alice-carousel__dots-item:hover {
  background-color:  #FCC444 !important;
}


.alice-carousel__dots-item.__active {
  background-color:  #FCC444 !important;
}

.alice-carousel__next-btn-item, .alice-carousel__prev-btn-item {
  display: none;
  background-color:  #FCC444 !important;
}

.alice-carousel__stage-item {
  width: 100% !important;
}
.images {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 100%;
}

.pagination {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
  font-size: 1.2rem;
  grid-gap: .5rem;
  gap: .5rem;
  color: #065471;
}
.pagination .page-num {
  padding: .5rem 1rem;
  cursor: pointer;
  border-radius: .5rem;
  font-weight: 600;
}

.pagination .page-num:hover {
  background-color: #FCC444;
  color: white;
}
.pagination .active {
  background-color: #FCC444;
}
html {
  background: hsla(190, 44%, 25%, 0.2);
  /* background: #fff; */
}

body {
  font-size: 10px;
  margin: 0;
}

* {
  font-family: 'Montserrat', sans-serif;
}

.main-page-box {
  display: flex;
  flex-flow: column;
  margin: 50px auto 0;
  width: 135em;
  overflow: hidden;
}

@media screen and (max-width: 1400px){
  html {
    font-size: 13px;
  }
  .main-page-box {
    width: 90%;
  }
}

